.button {
  margin-bottom: 15px;
  color: rgb(255, 255, 255);
  font-size: 20px;
  height: 72px;
  line-height: 1.2;
  border-radius: 6px;
  // display: inline-block
  text-align: center;
  vertical-align: middle;
  ::before {
      content: '';
      display: inline-block;
      height: 100%;
      vertical-align: middle; }
  img {
    width: 24px;
    position: relative;
    top: -4px; }
  @media screen and (min-width: 768px) {
    margin-top: 32px;
    width: 344px; } }

.button-window {
  @extend .button;
  background: #3581d5;
  margin-top: 32px;
  @media screen and (min-width: 768px) {
    margin-right: 16px; } }

.button-iphone {
  @extend .button;
  margin-top: 32px;
  background: #1962b2;
  @media screen and (min-width: 768px) {
    margin-left: 16px; } }
