.section4.container {
  .col-xs-12 {
    padding: 20px 0px 0px 0px; }

  @import ".././assets/buttons/style.sass";
  padding: 70px 17px;
  background: url("img/bg-color_4.png");
  width: 100%;
  p:first-child {
    font-size: 28px;
    font-weight: bold; }
  ul, p {
